/*export const defaultUrl = "http://ec2-3-76-10-149.eu-central-1.compute.amazonaws.com";*/
export const defaultUrl = "https://prihlasovani.novyp.org"

const sendReq = (
  opts: Object,
  url: string,
  endpoint: string,
  errMsgSetter?: Function
) => {
  return fetch(url + endpoint, opts)
    .then(async (res) => {
      let temp = await res.json();
      temp.status = res.status;
      return temp;
    })
    .catch((err) => {
      console.log("Hey there stranger! Dont mind these, some of them are even expected to happen :3")
      console.log(err);
      if (errMsgSetter) {
        errMsgSetter("Nco se pokazilo");
      }
    });
};

export const sendGetReq = async (
  endpoint: string,
  errMsgSetter?: Function,
  url: string = defaultUrl
) => {
  const opts = {
    method: "GET",
  };
  return await sendReq(opts, url, endpoint, errMsgSetter);
};

export const sendPostReq = async (
  endpoint: string,
  body: StrIndexedObj,
  errMsgSetter?: Function,
  url: string = defaultUrl,
) => {
  let postData = new FormData();
  Object.keys(body).forEach((key) => {
    postData.append(key, body[key]);
  });

  const opts = {
    method: "POST",
    body: postData,
  };
  return await sendReq(opts, url, endpoint, errMsgSetter);
};

/*
  Miscelaneous
*/
export const onChange = (data: Object, setData: Function, e: any) => {
  let temp = data;
  let id: keyof typeof temp = e.target.id;
  temp[id] = e.target.value;
  setData(temp);
};

export const compressObjects = (data: Object[], key: string) => {
  return data.map((o: { [key: string]: any }) => {
    return o[key];
  });
};

export const createArr = (len: number, defaultVal: any = null) => {
  return Array.apply(defaultVal, Array(len)).map(function () {
    return defaultVal;
  });
};

export const days = ["Pondl", "ter", "Steda", "tvrtek", "Ptek", "Sobota", "Nedle"]
export const day = (dayI: number) => {
  return days[dayI]
}

