import { Link, useLocation } from "react-router-dom";

function NoMatch() {

    let location = useLocation();

    return ( 
        <div className="container-fluid center-align column">
            <h3>Strnka <code>{location.pathname}</code> bu neexistuje, nebo na n nem pstup</h3>
            <Link to="/">
              <button
                type="button"
                className="btn btn-primary">
                Zpt na domovskou strnku
              </button>
            </Link>
        </div>
     );
}

export default NoMatch;