import { useEffect } from "react";
import { Course } from "../../Scripts/misc";
import { Modal } from "bootstrap";

interface Props {
    course: Course
}

function SignOutModals({course}: Props) {

    const successModalId = "successModal"+course.id
    const notSignedModalId = "notSignedModal"+course.id

    return ( <>

        {/* Sign out succesful modal*/}
    <div className="modal fade" id={successModalId} tabIndex={-1} aria-labelledby="successLabel">
    <div className="modal-dialog">
        <div className="modal-content">
            <div className="modal-header">
                <h5 className="modal-title" id="successLabel">Odhlen z krouku</h5>
                <button type="button" className="close" data-bs-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className="modal-body">
                spn jste se odhlsili z krouku {course.name}
            </div>
            <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal" onClick={() => {window.location.reload()}}>Ok</button>
            </div>
        </div>
    </div>
</div>

{/* Not signed into course modal modal*/}
<div className="modal fade" id={notSignedModalId} tabIndex={-1} aria-labelledby="signedLabel">
    <div className="modal-dialog">
        <div className="modal-content">
            <div className="modal-header">
                <h5 className="modal-title" id="signedLabel">Odhlen z krouku</h5>
                <button type="button" className="close" data-bs-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className="modal-body">
                Nejste na krouek {course.name} pihleni, take se z nj nemete ani odhlsit
            </div>
            <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Ok</button>
            </div>
        </div>
    </div>
</div>

    </> );
}

export default SignOutModals;